import React from 'react';


const Galleria = (gino) => {

let linkMOD ="";






	if(window.Scrollamelo) {
		const gino1 = window.document.getElementById(`${gino.props.currentid}`);
		if(gino1&&gino.props.modalita==="sfondogallery")	gino1.scrollIntoView()

	}




	for (var i = gino.props.robots.length - 1; i >= 0; i--) {
		if(Number(gino.props.currentid)===gino.props.robots[i].id)
		linkMOD=gino.props.robots[i].link;
	}

	const lInk = linkMOD;
	var pos = lInk.lastIndexOf("/");
	let oKGIF = true;
	const lInkpart2 = lInk.slice(pos, lInk.length);
	const lInkpart3 = lInkpart2.slice((lInkpart2.length-3), lInkpart2.length);
	if(lInkpart3==='gif')
		oKGIF= false;

	let lInkMOD = lInk;
		if(gino.props.type!=="mobile"){
			//console.log("desk///////////////")

		}else if(oKGIF){
			//console.log("mobile///////////////")
			const lInkpart1Mobile = lInk.slice(0, pos);
			const lInkpart2Mobile = lInk.slice(pos, lInk.length);
			lInkMOD = lInkpart1Mobile + "/okMobile" + lInkpart2Mobile;
			if(lInkMOD==="/okMobile")
				linkMOD = "";
			else
				linkMOD = lInkMOD;
		}




	return(
		<div	className={gino.display}
					>
			<div id={"topGalleria"} height={"0"}
					width={"0"}  className="zIndexhigh posAbsolute topzero" onClick={gino.outclick}>
				<img
						id={"x"}
						className={"xchiudi displayNone" + gino.display +" "+ gino.props.xshow}
						height={"60px"}
						width={"60px"}
						alt="x"
						src={`/foto/x.png`}

				/>



			</div>
			<div id={"leftGalleria"} height={"0"}
					width={"0"}  className="zIndexhigh posAbsolute leftzero" onClick={gino.outclick}></div>


			<img
					id={"currentimg"}
					className={"zIndexhigh opacitytopI alignCenter posAbsolute br3 displayNone" + gino.display}
					onLoad={gino.load}
					alt=""
					src={linkMOD}

			/>
			<div id={"rightGalleria"} height={"0"}
					width={"0"}  className="zIndexhigh posAbsolute rightzero" onClick={gino.outclick}></div>

			<div id={"bottomGalleria"} height={"0"}
					width={"0"}  className="zIndexhigh posAbsolute bottomzero" onClick={gino.outclick}></div>
		</div>


	);



}


export default Galleria;



